<template>
    <div class="system">
        <v-card class="system__card">
            <v-graph
                class="system__graph"
                hardware="cpu"
                :value="cpu.temp"
                unit="degree"
                :is-temp-normal="isNormal.cpu.temp"
                :is-load-normal="isNormal.cpu.load"
            >
                <template #gauge>
                    <the-gauge
                        id="cpu"
                        class="system__chart system__chart_type_cpu"
                        :value="cpu.load"
                        gradient-name="cpu"
                        :is-value-decreased="cpu.load <= previous.cpu"
                        :value-difference="difference.cpu"
                    />
                </template>

                <template #footer>
                    {{ getCPUFooterText }}
                </template>
            </v-graph>
        </v-card>

        <v-card class="system__card">
            <v-graph
                class="system__graph"
                hardware="gpu"
                :value="gpu.temp"
                unit="degree"
                :is-temp-normal="isNormal.gpu.temp"
                :is-load-normal="isNormal.gpu.load"
            >
                <template #gauge>
                    <the-gauge
                        id="gpu"
                        class="system__chart system__chart_type_gpu"
                        :value="gpu.load"
                        gradient-name="gpu"
                        :is-value-decreased="gpu.load <= previous.gpu"
                        :value-difference="difference.gpu"
                    />
                </template>

                <template #footer>
                    {{ getGPUFooterText }}
                </template>
            </v-graph>
        </v-card>

        <v-card class="system__card">
            <v-graph
                class="system__graph"
                hardware="ram"
                :value="ram[ram.length - 1]"
                unit="percent"
                :is-load-normal="isNormal.ram.load"
            >
                <template #gauge>
                    <the-chart-ram-container
                        class="system__chart system__chart_type_ram"
                        :ram-data="ram"
                    />
                </template>

                <template #footer>
                    {{
                        isLoadNormal(ram[ram.length - 1])
                            ? 'Ваша оперативная память в норме.'
                            : `Ваша оперативная память заполнена.`
                    }}
                </template>
            </v-graph>
        </v-card>
    </div>
</template>

<script>
import VCard from '@/components/VCard.vue';
import VGraph from '@/components/VGraph.vue';
import TheChartRamContainer from '@/components/charts/TheChartRamContainer.vue';
import TheGauge from '@/components/charts/TheGauge.vue';

export default {
    components: { VCard, VGraph, TheChartRamContainer, TheGauge },

    data() {
        return {
            limitTemp: 60,
            limitLoad: 70,

            previous: {
                cpu: 0,
                gpu: 0,
            },

            difference: {
                cpu: 0,
                gpu: 0,
            },
        };
    },

    computed: {
        cpu() {
            const { cpu } = this.$store.getters['telemetry/getTelemetryData'];

            return cpu;
        },

        gpu() {
            const { gpu } = this.$store.getters['telemetry/getTelemetryData'];

            return gpu;
        },

        ram() {
            const { ram } = this.$store.getters['telemetry/getTelemetryData'];

            return ram;
        },

        isNormal() {
            return {
                cpu: this.defineNormalValues(this.cpu),
                gpu: this.defineNormalValues(this.gpu),
                ram: this.defineNormalValues({
                    load: this.ram[this.ram.length - 1],
                }),
            };
        },

        getCPUFooterText() {
            if (this.isNormal.cpu.temp && this.isNormal.cpu.load) {
                return 'Ваш центральный процессор в норме.';
            }

            if (!this.isNormal.cpu.temp) {
                return `
                    Температура повышена на ${this.getIncreasedTemp(
                        this.cpu.temp
                    )}%.
                `;
            }

            return `
                Нагрузка повышена на ${this.getIncreasedLoad(this.cpu.load)}%.
            `;
        },

        getGPUFooterText() {
            if (this.isNormal.gpu.temp && this.isNormal.gpu.load) {
                return 'Ваш графический процессор в норме.';
            }

            if (!this.isNormal.gpu.temp) {
                return `
                    Температура повышена на ${this.getIncreasedTemp(
                        this.gpu.temp
                    )}%.
                `;
            }

            return `
                Нагрузка повышена на ${this.getIncreasedLoad(this.gpu.load)}%.
            `;
        },
    },

    watch: {
        cpu(newValue, oldValue) {
            this.previous.cpu = oldValue.load;

            this.difference.cpu = this.calculateDifference(
                newValue.load,
                oldValue.load
            );
        },

        gpu(newValue, oldValue) {
            this.previous.gpu = oldValue.load;

            this.difference.gpu = this.calculateDifference(
                newValue.load,
                oldValue.load
            );
        },
    },

    created() {
        this.$store.dispatch('telemetry/fetchTelemetry').then(() => {
            // Do something
        });

        setInterval(() => {
            this.$store.dispatch('telemetry/fetchTelemetry');
        }, 10000);
    },

    methods: {
        getIncreasedTemp(temp) {
            return Math.floor((100 * temp) / this.limitTemp - 100);
        },

        getIncreasedLoad(load) {
            return Math.floor((100 * load) / this.limitLoad - 100);
        },

        calculateDifference(newValue, oldValue) {
            const difference = Math.round((100 * newValue) / oldValue);

            if (difference >= Infinity) return 100;

            return difference;
        },

        defineNormalValues(data) {
            if (!data) return {};

            const result = {
                load: this.isLoadNormal(data.load),
            };

            if (data.temp !== undefined) {
                result.temp = this.isTempNormal(data.temp);
            }

            return result;
        },

        isTempNormal(temp) {
            return temp < this.limitTemp;
        },

        isLoadNormal(load) {
            return load < this.limitLoad;
        },
    },
};
</script>

<style lang="css" scoped>
.system {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 32px;
    width: 100%;
    min-height: 325px;
}

.system__card {
    width: 100%;
    max-width: 280px;
}

.system__chart {
    margin: 0 auto;
    margin-top: 17px;
    max-height: 155px;
    position: relative;
}

.system__chart_type_gpu {
    width: 100%;
}

.system__chart_type_ram {
    margin-top: 7px;
    left: -2px;
}
</style>
