<template>
    <div class="graph">
        <div class="graph__header">
            <span class="graph__title">
                <img
                    class="graph__icon"
                    :src="hardwareIconURL"
                    alt="Hardware icon"
                />

                <span class="graph__hardware">
                    {{ hardware }}
                </span>
            </span>

            <span class="graph__indicator">
                <img
                    v-if="unit === 'degree'"
                    class="graph__temp"
                    :src="tempIconURL"
                    alt="Temperature icon"
                />

                <span class="graph__value"> {{ value }} </span>

                <span :class="['graph__unit', `graph__unit_value_${unit}`]">
                    {{ unitSymbol }}
                </span>
            </span>
        </div>

        <div class="graph__gauge">
            <slot name="gauge"></slot>
        </div>

        <div class="graph__footer">
            <span
                :class="[
                    'graph__text',
                    'graph__text_margin',
                    isLoadNormal && isTempNormal
                        ? 'graph__text_color_valid'
                        : 'graph__text_color_invalid',
                ]"
            >
                *
            </span>

            <span class="graph__text">
                <slot name="footer"></slot>
            </span>
        </div>
    </div>
</template>

<script>
/* eslint-disable import/no-dynamic-require */
/* eslint-disable global-require */

import '@/assets/fonts/sf-ui-display/sf-ui-display.css';

export default {
    props: {
        hardware: {
            type: String,
            required: true,
        },

        value: {
            type: Number,
            required: true,
        },

        unit: {
            type: String,
            required: true,
        },

        isLoadNormal: {
            type: Boolean,
            required: true,
        },

        isTempNormal: {
            type: Boolean,
            default: true,
        },
    },

    computed: {
        hardwareIconURL() {
            return require(`@/assets/images/icons/${this.hardware}.svg`);
        },

        tempIconURL() {
            if (this.isTempNormal)
                return require('@/assets/images/icons/temp-low.svg');

            return require('@/assets/images/icons/temp-high.svg');
        },

        unitSymbol() {
            if (this.unit === 'degree') return '°';
            if (this.unit === 'percent') return '%';

            return '';
        },
    },
};
</script>

