<template>
    <div class="container">
        <the-chart-ram
            :width="235"
            :height="156"
            :chart-data="chartData"
            :options="options"
        />
    </div>
</template>

<script>
import TheChartRam from '@/components/charts/TheChartRam.vue';

import 'chartjs-plugin-style';

export default {
    name: 'TheChartRamContainer',

    components: { TheChartRam },

    props: {
        ramData: {
            type: Array,
            required: true,
        },

        max: {
            type: Number,
            default: 100,
        },

        min: {
            type: Number,
            default: 0,
        },
    },

    data: () => ({
        loaded: false,
        chartData: {},
        options: {
            layout: {
                padding: {
                    left: -10,
                    right: 0,
                    top: 0,
                    bottom: 0,
                },
            },
            repsonsive: false,
            maintainAspectRatio: false,
            elements: {
                point: {
                    radius: 0,
                },
            },
            legend: {
                display: false,
            },
            animation: {
                duration: 2000,
            },
            scales: {
                yAxes: [
                    {
                        ticks: {
                            beginAtZero: true,
                            display: false,
                            max: 105,
                            min: -5,
                        },
                        gridLines: {
                            display: false,
                            zeroLineColor: 'rgba(255, 255, 255, 0)',
                            zeroLineWidth: 0,
                            drawBorder: false,
                        },
                    },
                ],
                xAxes: [
                    {
                        ticks: {
                            beginAtZero: true,
                            display: false,
                        },
                        gridLines: {
                            color: 'rgba(255, 255, 255, 0)',
                            zeroLineColor: 'rgba(255, 255, 255, 0)',
                            drawBorder: false,
                        },
                    },
                ],
            },
        },
    }),

    computed: {
        gradientStroke() {
            const ctx = this.$children[0].$refs.canvas.getContext('2d');
            const gradientStroke = ctx.createLinearGradient(500, 0, 100, 0);

            gradientStroke.addColorStop(0, '#0b9bd0');
            gradientStroke.addColorStop(1, '#4fc1e9');

            return gradientStroke;
        },

        labels() {
            const { length = 0 } = this.ramData;

            return Array.from(' '.repeat(length));
        },
    },

    watch: {
        ramData() {
            this.fillData();
        },
    },

    mounted() {
        this.fillData();
    },

    methods: {
        fillData() {
            this.chartData = {
                labels: this.labels,
                datasets: [
                    {
                        shadowOffsetX: 0,
                        shadowOffsetY: 20,
                        shadowBlur: 10,
                        shadowColor: 'rgba(0, 0, 0, 0.5)',
                        outerGlowWidth: 10,
                        outerGlowColor: 'rgba(100, 210, 255, 0.5)',
                        borderColor: this.gradientStroke,
                        pointBorderColor: this.gradientStroke,
                        pointBackgroundColor: this.gradientStroke,
                        pointHoverBackgroundColor: this.gradientStroke,
                        pointHoverBorderColor: this.gradientStroke,
                        data: this.ramData,
                    },
                ],
            };
        },
    },
};
</script>

<style scoped>
.container {
    width: 235px;
    height: 156px;
    background-image: url('~@/assets/images/bitmaps/grid.png');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}
</style>
